import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import translation from "../views/translation";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'translation',
      component: translation
    }
  ]
})



